<template>
  <div class="loginForm">
    <h1>Авторизация</h1>
    <form @submit.prevent="loginForm">
      <div class="form-group">
        <label for="username">Имя пользователя:</label>
        <input type="text" v-model="username" required>
      </div>
      <div class="form-group">
        <label for="password">Пароль:</label>
        <input type="password" v-model="password" required>
      </div>
      <button type="submit">Войти</button>
    </form>

    <!-- Забыли свой пароль? -->
    <button @click="forgotPassword">Забыли свой пароль?</button>

    <!-- Кнопка для показа формы регистрации -->
    <button @click="showRegistrationForm">Регистрация</button>

    <!-- Форма регистрации -->
    <form v-if="showRegistration" @submit.prevent="register">
      <div class="form-group">
        <label for="newUsername">Новое имя пользователя:</label>
        <input type="text" v-model="newUsername" required>
      </div>
      <div class="form-group">
        <label for="newPassword">Новый пароль:</label>
        <input type="password" v-model="newPassword" required>
      </div>
      <button type="submit">Зарегистрироваться</button>
    </form>

  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      password: '',
      newUsername: '',
      newPassword: '',
      showRegistration: false
    };
  },
  methods: {
    loginForm() {
      console.log('Авторизация:', this.username, this.password);
    },
    register() {
      console.log('Регистрация:', this.newUsername, this.newPassword);
    },
    forgotPassword() {
      console.log('Забыли свой пароль?');
    },
    showRegistrationForm() {
    this.showRegistration = true;
    }
  }
};
</script>

<style>
.loginForm {
  max-width: 400px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.form-group{
    margin-bottom: 15px;
}
button{
    margin-bottom: 15px;
}

</style>

